.item--catalog {
    border-radius: 10px;
    background-color: #fff;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    
    box-sizing: border-box;
    
    width: 22%;
    height: 400px;

    padding: 20px 0;
    margin-bottom: 25px;

    color: #111;
    font-family: 'Merriweather';
    font-size: 18px;
    line-height: 27px; 

    &__img {
        width: 100%;
        height: 160px;
    }

    &__purchase-button {
        height: 40px;
        width: 100%;

        font-size: 16px;
        line-height: 24px; 
    }

    &__row:first-child {
        flex-basis: 85%;

        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;

        text-align: center;
        margin: 0px 10px;
    }

    &__row:last-child {
        width: 80%;
    }
}
